export default {
	data() {
		return {
			password: '',
			repassword: '',
			state: 2,
			mobile: '',
			showPassword: true,
			showPasswordTwo: true,
		};
	},
	components: {},
	computed: {},
	methods: {
		changePassword(type) {
			type==0?this.showPassword = !this.showPassword:this.showPasswordTwo = !this.showPasswordTwo
		},
		submit(){
			let data = {
				password:this.password,
				repassword: this.repassword
			}
			this.$store.dispatch('changePassword',data).then(res=>{
				uni.showToast({
					title: '修改成功',
					icon: 'none', //loading   none  success
					success: (res)=>{
						uni.removeStorageSync('token');
						setTimeout(function(){
							uni.reLaunch({
								url:'/pages/login/login'
							})																
						},1500)
					}
				});
				
			}).catch(err=>{})
		}
	},
}
